.footer {
    height: 60%;
    width: 100%;
    /* justify-content: space-around;
    align-items: center */
}

.footer-content-secondary {
    display: flex;
    flex-direction: column;
    padding-top: 1.8rem;
}

.tag {
    /* padding-top: 80px; */
    padding-bottom: 1rem;
    display: flex;

    justify-content: space-around;
    align-items: center;
    font-family: 'Brinnan';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
}

.footer-content-main {
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Brinnan';
    font-style: normal;
    padding-top: 2rem;
    font-weight: 400;
    font-size: 20px;
    line-height: inherit;
    color: #FFFFFF;
}

.contact-us-title {
    padding-bottom: 5px;
}

.contact-us li {
    /* list-style: none; */
    display: flex;
    flex-direction: column;
}

.map {
    height: 256px;
    width: 170px;
    clip-path: polygon(58% 0, 99% 23%, 100% 100%, 0 100%, 0 0);
}


.social-media {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Brinnan';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    /* padding-top: 30px; */
}

.social-media li {
    list-style: none;
    display: inline-block;
    padding: 0px 10px 10px 10px;

}

.social-media i {
    font-size: 14px;
    color: #FFFFFF;
}

.social-icons {
    font-size: 14px;
    border-radius: 0px;

    color: #6906B7;
    /* background-color: #FFFFFF;
    border: 2.5px solid #FFFFFF; */
}

