@media (max-width:420px) {
    .footer-content {
        display: flex;
        flex-direction: column;
        background-color: #6906B7 !important;
        padding: 10px;

    }

    .contact-us {
        font-size: 14px;
    }

    .footer-content-main {
        display: flex;
        flex-direction: column;
        margin-top: 10px;
        margin-bottom: 20px;
        font-size: 14px;
    }

    .map {
        height: 256px;
        width: 170px;
        clip-path: polygon(58% 0, 99% 23%, 100% 100%, 0 100%, 0 0);
        margin-top: 30px;
    }

    .social-icons {
        font-size:11px;
    }
    .social-media i {
        font-size: 14px;
        color: #FFFFFF;
    }
}